import axiosInstance from './index'

const axios = axiosInstance

export const getLessondata = () => {return axios.get(`http://localhost:8000/api/getLessondata/`)}

export const registerUser = (username, password) => {
    return axios.post(`http://localhost:8000/api/register/`, {'username': username, 'password': password})}

export const loginUser = (username, password) => {
    return axios.post(`http://localhost:8000/api/login/`, {'username': username, 'password': password})}

export const Insertlesson = (UserName, LessonName, TeachingContent, SituationAnalysis, 
    TeachingObjectives, TeachingImportant, TeachingProcess, LearningEvaluation, Homework) => {
    return axios.post(`http://localhost:8000/api/insertlesson/`, 
    {'UserName': UserName, 
     'LessonName': LessonName,
     'TeachingContent': TeachingContent, 
     'SituationAnalysis': SituationAnalysis,
     'TeachingObjectives': TeachingObjectives, 
     'TeachingImportant': TeachingImportant,
     'TeachingProcess': TeachingProcess, 
     'LearningEvaluation': LearningEvaluation,
     'Homework': Homework, 
    })}

    